import gql from "graphql-tag";

export const SITE_INFO = gql`
  query {
    site {
      name
    }
  }
`;

export const CURRENT_USER = gql`
  query ($username: String!) {
    currentUser(username: $username) {
      id
      username
      firstName
      lastName
      email
      avatar
      bio
      location
      website
    }
  }
`;

export const ASSET_METRICS = gql`
  query {
    assets {
      name
      marketcap
      tagline
      price
      percentChangeUsdLast24Hours
      stars
      fees
    }
  }
`;

export const ULTIMATE_ASSET_QUERY = gql`
  query UltimateAsset($symbol: String!) {
    ultimateAsset(symbol: $symbol) {
      name
      symbol
      logo
      tagline
      category
      description
      technology
      projectDetails
      consensusAlgorithm
      sector
      price
      percentChangeUsdLast24Hours
      percentChangeLast1Week
      marketcap
      volumeLast24Hours
      tvl
      tvlChange1d
      tvlChange7d
      feeTotal24h
      feeChange1d
      feeChange7d
      onchainActiveAddresses
      onchainAddressesCount
      supplyActive1d
      supplyInTop100Addresses
      supplyInTop1PercentAddresses
      supplyInTop10PercentAddresses
      circulatingSupply
    }
  }
`;


