<template>
  <LayoutAuthenticated>

    <!-- LOADING -->
    <SectionMain v-if="loading">
      <div class="text-center">
          <div role="status">
              <svg class="inline mr-2 w-8 h-8 text-gray-200 animate-spin dark:text-gray-600 fill-blue-600" viewBox="0 0 100 101" fill="none" xmlns="http://www.w3.org/2000/svg">
                  <path d="M100 50.5908C100 78.2051 77.6142 100.591 50 100.591C22.3858 100.591 0 78.2051 0 50.5908C0 22.9766 22.3858 0.59082 50 0.59082C77.6142 0.59082 100 22.9766 100 50.5908ZM9.08144 50.5908C9.08144 73.1895 27.4013 91.5094 50 91.5094C72.5987 91.5094 90.9186 73.1895 90.9186 50.5908C90.9186 27.9921 72.5987 9.67226 50 9.67226C27.4013 9.67226 9.08144 27.9921 9.08144 50.5908Z" fill="currentColor"/>
                  <path d="M93.9676 39.0409C96.393 38.4038 97.8624 35.9116 97.0079 33.5539C95.2932 28.8227 92.871 24.3692 89.8167 20.348C85.8452 15.1192 80.8826 10.7238 75.2124 7.41289C69.5422 4.10194 63.2754 1.94025 56.7698 1.05124C51.7666 0.367541 46.6976 0.446843 41.7345 1.27873C39.2613 1.69328 37.813 4.19778 38.4501 6.62326C39.0873 9.04874 41.5694 10.4717 44.0505 10.1071C47.8511 9.54855 51.7191 9.52689 55.5402 10.0491C60.8642 10.7766 65.9928 12.5457 70.6331 15.2552C75.2735 17.9648 79.3347 21.5619 82.5849 25.841C84.9175 28.9121 86.7997 32.2913 88.1811 35.8758C89.083 38.2158 91.5421 39.6781 93.9676 39.0409Z" fill="currentFill"/>
              </svg>
              <span class="sr-only">Loading...</span>
          </div>

      <div role="status" class="space-y-8 animate-pulse md:space-y-0 md:space-x-8 md:flex md:items-center">
          <div class="flex justify-center items-center w-full h-48 bg-gray-300 rounded sm:w-96 dark:bg-gray-700">
              <svg class="w-12 h-12 text-gray-200" xmlns="http://www.w3.org/2000/svg" aria-hidden="true" fill="currentColor" viewBox="0 0 640 512"><path d="M480 80C480 35.82 515.8 0 560 0C604.2 0 640 35.82 640 80C640 124.2 604.2 160 560 160C515.8 160 480 124.2 480 80zM0 456.1C0 445.6 2.964 435.3 8.551 426.4L225.3 81.01C231.9 70.42 243.5 64 256 64C268.5 64 280.1 70.42 286.8 81.01L412.7 281.7L460.9 202.7C464.1 196.1 472.2 192 480 192C487.8 192 495 196.1 499.1 202.7L631.1 419.1C636.9 428.6 640 439.7 640 450.9C640 484.6 612.6 512 578.9 512H55.91C25.03 512 .0006 486.1 .0006 456.1L0 456.1z"/></svg>
          </div>
          <div class="w-full">
              <div class="h-2.5 bg-gray-200 rounded-full dark:bg-gray-700 w-48 mb-4"></div>
              <div class="h-2 bg-gray-200 rounded-full dark:bg-gray-700 max-w-[480px] mb-2.5"></div>
              <div class="h-2 bg-gray-200 rounded-full dark:bg-gray-700 mb-2.5"></div>
              <div class="h-2 bg-gray-200 rounded-full dark:bg-gray-700 max-w-[440px] mb-2.5"></div>
              <div class="h-2 bg-gray-200 rounded-full dark:bg-gray-700 max-w-[460px] mb-2.5"></div>
              <div class="h-2 bg-gray-200 rounded-full dark:bg-gray-700 max-w-[360px]"></div>
          </div>
          <span class="sr-only">Loading...</span>
      </div>

            </div>
      </SectionMain>

      <!-- LOADING END -->
    <SectionMain v-else>
      <!-- BREADCRUMB -->
      <BreadCrumb :projectName="asset.name" class="py-4"/>
      <!-- PROJECT NAME TAGLINE TICKER CATEGORY-->
      <div class="py-4 flex flex-col justify-between">
        <ProjectName
            :project="asset.name"
            :tagline="asset.messari_tagline"
            :sector="asset.messari_sector"
            :ticker="asset.symbol"
            :logo="asset.logo ? asset.logo : 'https://cdn4.iconfinder.com/data/icons/leto-weather/64/na_not_available_weather_sun-512.png'"
          />
        </div>

        <!-- GENERAL PROTOCOL STATS -->
        <ProjectHead :cardBoxWidgets="cardBoxWidgets" />
        <!-- PROJECT PRICE CHART -->
        <ProjectPriceChart :asset="asset" />
        <!-- DEEP PROTOCOL METRICS -->
        <ProjectInfo :asset="asset" />
    
    </SectionMain>
  </LayoutAuthenticated>
</template>
<script>
import { reactive } from "vue";
import SectionMain from "@/components/SectionMain.vue";
import ProjectHead from "@/components/ProjectHead.vue";
import LayoutAuthenticated from "@/layouts/LayoutAuthenticated.vue";
import BreadCrumb from "@/components/BreadCrumb.vue";
import ProjectInfo from "@/components/ProjectInfo.vue";
import ProjectName from "@/components/ProjectName.vue";
import ProjectPriceChart from "@/components/ProjectPriceChart.vue";
import { mdiTrendingUp, mdiMail } from "@mdi/js";
import { supabase } from "../supabase";

export default {
  components: {
    SectionMain,
    ProjectHead,
    LayoutAuthenticated,
    BreadCrumb,
    ProjectInfo,
    ProjectName,
    ProjectPriceChart,
    mdiTrendingUp,
    mdiMail,
  },
  name: "ProjectView",
  data() {
    return {
      asset: {},
      loading: true,
    };
  },
  async created() {
    // Fetch data when the component is first created
    this.fetchData();
  },
  watch: {
    // Watch the $route object for changes
    $route() {
      // Fetch data when the route changes
      this.fetchData();
      this.loading = true;
    },
  },
  methods: {
    async fetchData() {
      let symbol = this.$route.params.ticker.toUpperCase()
      // Fetch the asset data from the first table
      let { data: ultimateframe, error } = await supabase
        .from('ultimateframe')
        .select()
        .eq('symbol', symbol)

      console.log('ultimateframe; ', ultimateframe)

      if (error) return console.error("result" + error);

      // Fetch the dataset from the second table
      let { data: jsoncoin, errors } = await supabase
        .from('jsoncoin')
        .select('payload')
        .order('id', { ascending: false });

      console.log('jsoncoin', jsoncoin)
      if (errors) return console.error("result" + errors);

      // Filter the dataset for the specific symbol
      this.dataset = jsoncoin[0]['payload'].filter(item => item.symbol === symbol)
      this.dataset = this.dataset[0]

      // Merge the two objects into one
      this.asset = { ...ultimateframe[0], ...this.dataset };
      this.loading = false;

      // Update the card box widgets data
      this.cardBoxWidgets = [
      {
        prefix: "$",
        trend: this.asset.percent_change_usd_last_24_hours ? parseFloat(this.asset.percent_change_usd_last_24_hours).toFixed(2) + "%" : "N/A",
        trendType: this.asset.percent_change_usd_last_24_hours ? (this.asset.percent_change_usd_last_24_hours >= 0 ? "up" : "down") : "info",
        color: "text-emerald-500",
        label: "Price",
        suffix: "",
        number: parseFloat(this.asset.price).toFixed(2),
        icon: "https://cdn4.iconfinder.com/data/icons/business-1221/24/Inflation-512.png"
      },
      {
        prefix: "$",
        trend: "N/A",
        trendType: "info",
        color: "text-emerald-500",
        label: "Marketcap",
        suffix: this.asset.marketcap >= 1000000000 ? "B" : "M",
        number: this.asset.marketcap >= 1000000000 ? (this.asset.marketcap / 1000000000).toFixed(0) : (this.asset.marketcap / 1000000).toFixed(0),
        icon: "https://cdn0.iconfinder.com/data/icons/cryptocurrency-63/64/Market-capitalization-global-bitcoin-cryptocurrency-512.png"
      },
      {
        prefix: "$",
        trend: this.asset.tvl_change_1d ? parseFloat(this.asset.tvl_change_1d).toFixed(2) + "%" : "N/A",
        trendType: this.asset.tvl_change_1d ? (this.asset.tvl_change_1d >= 0 ? "up" : "down") : "info",
        color: "text-emerald-500",
        label: "TVL",
        suffix: "B",
        number: (this.asset.tvl / 1000000000).toFixed(0),
        icon: "https://cdn2.iconfinder.com/data/icons/business-and-commercial-mixed-hexagone/128/33-512.png"
      },
      {
        prefix: "$",
        trend: this.asset.fee_change_1d ? parseFloat(this.asset.fee_change_1d).toFixed(2) + "%" : "N/A",
        trendType: this.asset.fee_change_1d ? (this.asset.fee_change_1d >= 0 ? "up" : "down") : "info",
        color: "text-emerald-500",
        label: "Fees 24h",
        suffix: this.asset.fee_total24h >= 1000000 ? "M" : this.asset.fee_total24h >= 1000 ? "K" : "",
        number: this.asset.fee_total24h >= 1000000 ? (this.asset.fee_total24h / 1000000).toFixed(0) : this.asset.fee_total24h >= 1000 ? (this.asset.fee_total24h / 1000).toFixed(0) : this.asset.fee_total24h,
        icon: "https://cdn3.iconfinder.com/data/icons/finance-and-business/64/fees-money-payment-cash-512.png"
      }
    ]
    },
  },
};
</script>