<template>
  <div>
    <!-- NAVIGATION -->
    <ul class="flex flex-wrap text-sm font-medium text-center text-gray-500 border-b border-gray-200 dark:border-gray-700 dark:text-gray-400">
      <li v-for="(tab, index) in tabs" :key="index" v-bind:class="{ 'active': selectedTab === tab.id }">
        <a :href="tab.link" 
          :class="tab.class"
          v-on:click.prevent="selectTab(tab.id)">{{ tab.name }}</a>
      </li>
    </ul>

  <div class="py-2 grid grid-cols-1 lg:grid-cols-1 gap-6">
     <!-- <BreadCrumbProject :tabName="selectedTab"/>--> 
      <component v-bind:is="selectedTabComponent" :asset="asset"></component>
  </div>
</div>

</template>
<script> 
import CardBox from "@/components/CardBox.vue";
import BreadCrumbProject from "./BreadCrumbProject.vue";
import Overview from "@/components/ProjectOverview.vue";
import Profile from "@/components/ProjectProfile.vue";
import Metrics from "@/components/ProjectRevenue.vue";
import Insights from "@/components/CoinTable.vue";
import Research from "@/components/ProjectRevenue.vue";

export default {
components: {
  CardBox,
  BreadCrumbProject
},
name: 'ProjectInfo',
props: ['asset'],
data() {
  return {
    selectedTab: 'overview',
    selectedTabComponent: Overview,
    tabs: [
      { id: 'overview', name: 'Key metrics', link: '#', class: 'inline-block p-4 rounded-t-lg hover:text-gray-600 hover:bg-gray-50 dark:hover:bg-gray-800 dark:hover:text-gray-300' },
      { id: 'profile', name: 'Profile', link: '#', class: 'inline-block p-4 rounded-t-lg hover:text-gray-600 hover:bg-gray-50 dark:hover:bg-gray-800 dark:hover:text-gray-300' },
      { id: 'fees', name: 'Revenue', link: '#', class: 'inline-block p-4 rounded-t-lg hover:text-gray-600 hover:bg-gray-50 dark:hover:bg-gray-800 dark:hover:text-gray-300' },
      { id: 'media', name: 'Media', link: '#', class: 'inline-block p-4 rounded-t-lg hover:text-gray-600 hover:bg-gray-50 dark:hover:bg-gray-800 dark:hover:text-gray-300' },
      { id: 'developer', name: 'Developer Activity', link: '#', class: 'inline-block p-4 rounded-t-lg hover:text-gray-600 hover:bg-gray-50 dark:hover:bg-gray-800 dark:hover:text-gray-300' },
      { id: 'exchange_flows', name: 'Exchange Flows', link: '#', class: 'inline-block p-4 text-gray-400 rounded-t-lg cursor-not-allowed dark:text-gray-500', disabled: true },
      { id: 'onchain_analytics', name: 'Onchain Analytics', link: '#', class: 'inline-block p-4 text-gray-400 rounded-t-lg cursor-not-allowed dark:text-gray-500', disabled: true },
      { id: 'charts', name: 'Charts', link: '#', class: 'inline-block p-4 text-gray-400 rounded-t-lg cursor-not-allowed dark:text-gray-500', disabled: true }
            ]
  };
},
methods: {
  selectTab(tab) {
    this.selectedTab = tab
    switch(tab) {
      case 'overview':
        this.selectedTabComponent = Overview
        break
      case 'profile':
        this.selectedTabComponent = Profile
        break
      case 'fees':
        this.selectedTabComponent = Metrics
        break
      case 'media':
        this.selectedTabComponent = Insights
        break
      case 'developer':
        this.selectedTabComponent = Research

        break
    }
  },
  selectedTabAriaCurrent(tab) {
    return this.selectedTab === tab
  },
}
};
</script>